<template>
  <div class="toc">
    <div class="toc-title">{{toc[0].documents[0].title}}</div>
    <div v-for="(group, iG) in toc" :key=getKey(iG)>
      <div class="toc-subheading">{{group.title}}</div>
      <div v-for="(document, iD) in group.documents" class="toc-item" 
        :key=getKey(iG,iD) v-on:click="selectDocument(document.id)">
        {{iD + 1}} - {{document.title}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'TOC',
  props:['toc'],
  methods:{
    getKey(g, d){
      let key = 'g' + g
      if(!isNaN(d)){
        key += 'd' + d
      }
      return key;
    },
    selectDocument(id){
      this.$parent.selectDocument(id)
    }
  },
  mounted(){
    console.log(this.toc);
  }
}
</script>
<style lang="scss" scoped>
.toc-title{
  font-size:1.8em;
  font-weight:bold;
  text-align:center;
  margin:1em auto 1em auto;
  width:60%;
}
.toc-subheading{
  font-size:1.5em;
  font-weight:bolder;
  text-align:center;
  padding:1em 0 1em 0;
}
.toc-item{
  border-bottom:1px lightgray solid;
  padding:0.25em 2em 0.25em 2em;
  text-align:left;
}
.toc-item:hover{
  background-color:rgba(0,0,0,0.1);
}
.toc-item:active{
  background-color:rgba(0,0,0,0.25);
}

@media (max-width:450px) {
  .toc{
    margin:0.5em;
  }
}
@media (min-width:800px) {
  .toc{
    margin:3em;
  }
}

</style>