import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Converter from '../views/Converter.vue'
import Reader from '../views/Reader.vue'
import ReaderMenu from '../views/ReaderMenu.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/converter',
    name: 'Converter',
    component: Converter
  },
  {
    path: '/reader',
    name: 'ReaderMenu',
    component: ReaderMenu
  },
  {
    path: '/reader/:id',
    name: 'Reader',
    component:Reader,
    props:true
  }
]

const router = new VueRouter({
  routes
})

export default router
