<template>
<span>
  <span v-if="!token.is_token" class="non-token">{{token.text}}</span>
  <span v-else>
    <ruby v-if="'composition' in token" class="token">
      <span class="composition">
        <span class="lemma" @click="select()"
                            :key="getKey()">{{token.composition.lemma.text}}<span v-if="isSelected&&(getKey()==selectedComponentId)" 
                            :class="['annotation-holder',isLeftJustified ? 'annotation-lhs':'annotation-rhs']">
            <Annotation :entries=token.composition.lemma.items />
          </span>
        </span>
        <span class="suffix" v-for="(suffix,i) in token.composition.suffixes" 
              :key="getKey(i)" @click="select(i)">{{suffix.text}}<span v-if="isSelected&&(getKey(i)==selectedComponentId)" class="annotation-holder" 
              :class="['annotation-holder',isLeftJustified ? 'annotation-lhs':'annotation-rhs']">
            <Annotation :entries=suffix.items />
          </span>
        </span>
      </span>
      <rt v-if="hasInterlinear" class="interlinear">{{token.composition.lemma.items[0].meaning}}</rt>
    </ruby>
    <span v-else>
      <span class="token unidentified">{{token.text}}</span>&nbsp;
    </span>
  </span>
</span>
</template>
<script>
import { bus } from '../main';
import Annotation from './Annotation.vue'
export default {
  name:'Token',
  props:['token','tokenId', 'isSelected','hasInterlinear'],
  components:{
    Annotation
  },
  data:function(){
    return {
      selectedComponentId:null,
      isLeftJustified:true
    }
  },
  computed:{},
  methods:{
    getKey:function(index){
      let key = this.tokenId;
      if(isNaN(index)) return key + '-l';
      return key + '-s' + index;
    },
    select:function(i){
      this.isLeftJustified = (this.$el.offsetLeft + this.$el.offsetWidth / 2) < (window.innerWidth / 2);
      let componentId = this.getKey(i);
      if(componentId == this.selectedComponentId){
        bus.$emit('update-selected', null);
        this.selectedComponentId = null;
      } else{
        bus.$emit('update-selected', this.tokenId);
        this.selectedComponentId = componentId;
      }
    }
  },
  mounted(){

  }
}
</script>
<style scoped>
.selected{
  border:1px solid red;
}
.token{
  background-color:rgb(0,0,0,0.15);
  color:black;
  padding:0 2px 0px 2px;
  border-radius:0.3em;
  margin:0 0.1em 0 0.1em;
  width:max-content;
}
.token:hover{
  background-color:rgb(0,0,0,0.01);
}
.composition{
  white-space:nowrap;
  display:flex;
  flex-direction:row;
  margin:.5em 0 .5em 0;
}
.annotation-holder{
  position:absolute;
  top:1.5em;
  z-index:200;
  white-space:nowrap;
  background-color:white;
  width:200px;
  box-shadow:gray 0.1em 0.1em 0.5em;
}
.annotation-lhs{
  left:0em;
}
.annotation-rhs{
  right:0em;
}
.unidentified{
  background-color:maroon;
  color:white;
  white-space:nowrap;
}
.unidentified:hover{
  background-color:pink;
  color:maroon;
}
.lemma{
  padding-right:2px;
  white-space:nowrap;
  position:relative;
  flex-grow:1;
  text-align:right;
}
.lemma:last-child {
  text-align:center;
}
.suffix{
  border-left:1px dotted black;
  padding-left:1px;
  padding-right:1px;
  background-color:rgba(0,0,0,0.1);
  white-space:normal;
  position:relative;
}
.suffix:last-child{
  flex-grow:1;
}
.interlinear{
  text-transform:none;
  font-weight:bolder;
  font-style:italic;
  text-align:left;
  max-width:10em;
  white-space:nowrap;
  overflow:hidden;
  text-overflow:ellipsis;
}
.non-token{
  white-space:normal;
}
</style>