<template>
<div>
  <div v-if="collection">
    <Collection converted=true :collection="collection"/>
  </div>
  <div v-else class="converter">
    <h1>Converter</h1>

    <div>Copy Uyghur-language text or URL for conversion.</div>
    <div class="warning-box">
      <div class="warning-text">WARNING</div>
      <div class="disclaimer-text">By clicking 'paste' you agree to send your clipboard contents for conversion in the cloud. Information received by the app is anonymized and will only be used to improve the app's performance.</div>
      <div class="input-holder">
        <div class="input-text">
          <div>{{inputText}}</div>
        </div>
        <div class="go-button" @click=paste>Paste</div>
      </div>
    </div>

  </div>
</div>
</template>

<script>
import Collection from '@/components/Collection'
export default {
  name:'Converter',
  components:{
    Collection
  },
  data:function(){
    return {
      collection:null,
      inputText:''
    }
  },
  methods:{
    getType(text){
      let tokens = text.split(" ");
      let type = 'text';
      if(tokens.length < 4){
        tokens.forEach(token => {
          if(token.match(/\w\.\w/)) type = 'url';
        });
      }
      return type;
    },
    paste(){
      navigator.clipboard.readText().then(text => {
        this.inputText = text;
        this.updateCollection(text, this.getType(text));
      });
    },
    updateCollection(text, type){
      if(!type) type = 'text';
      const url = 'https://us-central1-warm-mediator-289819.cloudfunctions.net/test-func';
      const request = {
        method:'POST',
        headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body:JSON.stringify({
          'text':text,
          'type':type
        })
      };
      let that = this;
      fetch(url, request)
        .then(response => {
          response.json().then(data => {
            that.collection = data.collection;
            //that.selectDocument(0);
          });
        });
    },
  }
}
</script>
<style lang="scss" scoped>
.document-buttons{
  line-height:1.5em;
  position:sticky;
  top:0;
  background-color:rgba(255,255,255,0.8);
  padding:1em;
  z-index:100;
}
.document-button{
  background-color:red;
  border-radius:0.2em;
  color:white;
  padding:0 0.2em 0 0.2em;
  margin:0.2em;
}
.warning-box{
  border:1px solid red;
  padding:1em;
}
.disclaimer-text{
  font-weight:bolder;
  font-size:0.8em;
  margin:1em;
}
.warning-text{
  color:red;
  font-weight:bold;
  text-align:center;
  width:100%;
}
.input-holder{
  display:flex;
  flex-direction:row;
}
.input-text{
  border-radius:.25em;
  flex-grow:1;
  border:1px dotted gray;
  margin:0.5em;
  font-size:0.7em;
  background-color:lightgray;
  vertical-align:middle;
  padding:0.5em;
  height:3em;
  overflow-y:auto;
}
@media (max-width:450px) {
  .warning-box {
    margin:1em 0.5em 1em 0.5em;
  }
}
@media (min-width:800px) {
  .warning-box {
    margin:2em auto 2em auto;
    width:720px;
  }
}
</style>