<template>
  <div id="app">
    <div v-if="selectedDocument!=-1" class="buttons-holder">
      <div v-if=converted class="button-holder">
        <div class="circular-button" @click=reloadRoute>
          <i class="icon material-icons not-selectable">replay</i>
        </div>
      </div>
      <div class="button-holder">
        <div class="circular-button" @click=toggleInterlinear>
          <i v-if="hasInterlinear" class="icon material-icons small-icon not-selectable">speaker_notes_off</i>
          <i v-else class="icon material-icons not-selectable">speaker_notes</i>
        </div>
      </div>
      <div v-if="hasMultipleChapters" class="button-holder">
        <div class="circular-button" @click=selectDocument(-1)><i class="icon material-icons small-icon not-selectable">format_list_numbered</i></div>
      </div>
      <div v-if="hasMultipleChapters&&hasPrevious" class="button-holder">
        <div class="circular-button" @click="selectDocument(documentId - 1)"><i class="icon material-icons small-icon not-selectable">skip_previous</i></div>
      </div>
      <div v-if="hasMultipleChapters&&hasNext" class="button-holder">
        <div class="circular-button" @click="selectDocument(documentId + 1)"><i class="icon material-icons small-icon not-selectable">skip_next</i></div>
      </div>
    </div>
    <div v-if="selectedDocument!==null">
      <div v-if="selectedDocument==-1" class="toc-holder">
        <TOC v-if="collection" :toc="collection.toc"/>
      </div>
      <div v-else>
        <Document v-bind:document="selectedDocument" v-bind:hasInterlinear="hasInterlinear"/>
      </div>
    </div>
  </div>
</template>

<script>
import Document from '../components/Document.vue';
import TOC from '../components/TOC.vue';
export default {
  name: 'App',
  props:['collection','converted'],
  components: {
    Document, TOC
  },
  data:function(){
    return {
      entries:[],
      position:{x:0, y:0},
      selectedDocument:null,
      popupIsVisible:false,
      inputText:'',
      hasInterlinear:false,
      openWindowNo:-1,
      hasMultipleChapters:false,
      documentId:null,
      hasPrevious:null,
      hasNext:null
    }
  },
  methods:{
    reloadRoute(){
      this.$router.go()
    },
    toggleInterlinear(){
      console.log('toggleinterlinear')
      this.hasInterlinear = !this.hasInterlinear;
    },
    selectDocument(index){
      console.log('select');
      if(this.hasMultipleChapters){
        if(index == -1){
          this.selectedDocument = -1;
          this.documentId = -1;
          this.hasNext = false;
          this.hasPrevious = false;
        } else {
          this.selectedDocument = this.collection.documents[index];
          this.documentId = index;
          this.hasNext = index < this.collection.documents.length - 1;
          this.hasPrevious = index > 0;
        }
      } else {
        this.selectedDocument = this.collection.documents[0];
        this.documentId = 0;
        this.hasNext = false;
        this.hasPrevious = false;
      }
      window.scrollTo(0,0);
    },
  },
  mounted(){
    console.log(this.converted)
    if(this.collection.documents.length > 1) this.hasMultipleChapters = true;
    this.selectDocument(-1);
  }
}
</script>

<style>
.buttons-holder{
  position:fixed;
  bottom:1em;
  right:1em;
  z-index:500;
}
.button-holder{
  display:flex;
  flex-direction:row;
  justify-content:flex-end;
}
@keyframes wobble{
  from {
    padding-right: 0.48em;
  }
  to {
    padding-right:0.52em;
  }
}
.go-button{
  padding:0;
  background-color:red;
  padding:0.5em;
  color:white;
  border-radius:.5em;
  flex-grow:0;
  margin:0.5em;
}
.toc-holder{
  margin:2em;
}
.circular-button{
  background-color:#500000;
  color:white;
  padding:.5em;
  border-radius:2em;
  margin-bottom:.5em;
  box-shadow:grey 0.25em 0.25em 1em;
}

.material-icons {
  /* Preferred icon size = 24px */
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
.small-icon{
  font-size:1.5em;
}
.big-icon{
  font-size:3em;
}

/* This prevents text such as button text from being selected. */
.not-selectable {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

@media (max-width:600px) {
  .window {
    top:1em;
    right:1em;
    bottom:1em;
    left:1em;
  }
  .window-content{
    margin:0.5em;
  }
}
@media (min-width:800px) {
  .window {
    margin:auto;
    width:600px;
  }
  .buttons-holder{
    right:5vw;
  }
}
</style>
