<template>
  <div class="document">
    <div :class="paragraph.class" v-for="(paragraph, h) in document.paragraphs" :key="getKey(h)">
      <span class="sentence" v-for="(sentence,i) in paragraph.sentences" :key="getKey(h,i)">
        <Token v-for="(token, j) in sentence.tokens" :tokenId="getKey(h,i,j)"
              :token=token
              :key="getKey(h,i,j)"
              :isSelected="selectedTokenId==getKey(h,i,j)"
              :hasInterlinear="hasInterlinear"/>
      </span>
    </div>
  </div>
</template>

<script>
import Token from './Token.vue';
import { bus } from '../main';
export default {
  name: 'Document',
  props:['document', 'hasInterlinear'],
  data:function(){
    return {
      selectedTokenId:null
    }
  },
  components: {
    Token
  },
  methods:{
    getKey:function(h,i,j){
      let key = 'h' + h;
      if(!isNaN(i)){
        key += 'i' + i;
        if(!isNaN(j)){
          key += 'j' + j;
        }
      }
      return key;
    }
  },
  mounted(){

/* 
    TODO: 
      Does alternative stem form block derived forms? YES!
      qaranga! be/qinga! etc.
      arabic reversal character
      Clustered non-tokens have spaces removed e.g. "?70" at sentence boundary
      ölüdighan; körüdighan =>>> -üdighan ?
      =ghay imperative suffix
      -gech = since/because suffix
      -ghila, -ghimu (suffixes added after gha not recognized)
      -p needs to be expanded to include vowel? e.g. qilin+ip
      In cyrl does -u/l- becom -il- ?
      -iy adj. suffix.
      - sözlelmeydu ?? ending??
      - bilgüngiz
      -  éduq
*/
    bus.$on('update-selected', tokenId => {
        this.selectedTokenId = tokenId;
    })
  }
}
</script>
