<template>
  <div v-if="collection" class="reader">
    <Collection :collection="collection"/>
  </div>
</template>
<script>
import Collection from '@/components/Collection';
export default {
  name:'Reader',
  components:{
    Collection
  },
  data:function(){
    return {
      collection:null
    }
  },
  props:['id'],
  mounted(){
    console.log(JSON.stringify(this.id));
    const url = "https://nearlyready.github.io/apps/ASSETS/uyghur-lemmatizer/" + this.id + ".json";
    console.log(url);
    fetch(url)
    .then(response => response.json())
    .then(data => {
      this.collection = data;
      //if(this.collection.documents.length > 0) this.hasMultipleChapters = true;
      //this.selectDocument(-1);
    })
    .catch(error => { console.log(error); this.isErrorResponse = true;});
  }
}
</script>